    export default{
        data(){
            return{
                isInput:false,//是否输入账号,
				isSame:true,
				designer:{
					pwd:'',
					confirmPwd:''
				},
				sameStyle:{
					color:'red'
				}
            }
        },
        components:{

        },
        onLoad(){

        },
		methods:{
			//验证密码
			checkPwdEvent(event){
				if(event){
					var reg=/^[a-zA-Z0-9]{6,12}$/;   
					if(reg.test(event)==false){
						uni.showToast({
							title:'密码不能含有非法字符，长度在6-12之间',
							icon:'none'
						})
					}else{
						this.checkPwd=true;
					}
					
				}
			},
			checkConfirmEvent(event){
				var _this=this;
				if(this.designer.pwd!=event){
					_this.isSame=false;
					uni.showToast({
						title:'两次输入的密码不一致',
						icon:'none'
					})
				}
			}
		}
    }
